<template>
  <div class="hello">
    <div class="my-row">
      <h3>
        绑定手机
      </h3>
    </div>
    <div class="my-row">
        你的手机号只会用来作为登录绑定使用，不会用作其他用途
    </div>
    <b-form action="/login" method='post' @submit="onSubmit" @reset="onReset">
      <b-alert :show="backendError.length != 0" variant="danger" >{{backendError}}</b-alert>
      <b-alert :show="!validPhone" variant="danger" >请输入有效的手机号</b-alert>
      <b-alert :show="validPhone && !validCode" variant="danger">请输入有效的验证码</b-alert>
      <b-input-group prepend="手机号">
        <b-form-input v-model="form.phone"
                type="tel"
                maxlength="11"
                placeholder="请输入手机号"
                name="phone"
                :state="validPhone">
        </b-form-input>
      </b-input-group>
      <br>
      <b-input-group prepend="验证码">
        <b-form-input v-model="form.code"
                type="tel"
                maxlength="6"
                name="verifyCode"
                placeholder="请输入六位验证码">
        </b-form-input>
        <b-input-group-append>
          <b-btn variant="outline-success" @click="requestCode" :disabled="requestInterval>0 || !validPhone">{{codeText}}</b-btn>
        </b-input-group-append>
      </b-input-group>
      <br>
      <b-button type="submit" variant="primary" :disabled="!validPhone || !validCode" size="lg" class="btn-block">绑定</b-button>

    </b-form>
  </div>
</template>

<script>

export default {
  name: 'Login',
  data () {
    return {
      form: {
        phone: '',
        code: ''
      },
      requestInterval: 0,
      backendError: ''
    }
  },
  computed: {
    codeText: function () {
      if (this.requestInterval > 0) {
        return `获取验证码（${this.requestInterval}秒）`
      } else {
        return '获取验证码'
      }
    },
    validPhone: function () {
      return /^1(3|4|5|7|8|9)\d{9}$/.test(this.form.phone)
    },
    validCode: function () {
      return /^\d{6}$/.test(this.form.code)
    }
  },
  methods: {
    requestCode (evt) {
      this.$http.post('/request/code',
        {
          phone: this.form.phone
        })
        .then(function (response) {
          console.log(response)
          this.restartRequestInterval()
        })
        .catch((err) => {
          console.log(err)
        })
    },

    onSubmit (evt) {
      this.$http.post('/login',
        this.form)
        .then(response => {
          if (response.data.result === 'redirect_to_auth') {
            console.log('Ok')
            this.$router.push('/Grant')
          } else {
            this.backendError = response.data.result
          }
        }).catch(err => {
          this.backendError = 'something wrong'
          console.log(err)
        })
      evt.preventDefault()
    },

    onReset (evt) {
      evt.preventDefault()
    },

    restartRequestInterval: function () {
      this.requestInterval = 60
      clearInterval(this.intervalId)
      this.intervalId = setInterval(() => {
        this.requestInterval--
        if (this.requestInterval === 0) {
          clearInterval(this.intervalId)
        }
      }, 1000)
    }
  },

  destroyed: function () {
    console.log('destroyed')
    clearInterval(this.intervalId)
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

a {
  color: #42b983;
}

.my-row {
  margin-bottom: 20px
}

</style>



