import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Grant from '@/components/Grant'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
    {
      path: '/grant',
      name: 'Grant',
      component: Grant
    }
  ]
})



